import { Cars } from '../types/cars';

export enum Sorting {
  Default = ' ',
  YearRelease = ' ',
  Price = '',
}

export type SortName = keyof typeof Sorting;

export const Comparator: { [key in SortName]: (a: Cars, b: Cars) => number } = {
  Default: () => 0,
  YearRelease: (a, b) => a.year - b.year,
  Price: (a, b) => a.price - b.price
}

